.about-content {
  display: flex;
  height: 82vh;
  width: 100%;
  gap: 200px;
  justify-content: center;
  align-items: center;
}

.about-title {
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: flex-start;
  justify-content: center;
}

.about-title hr {
  width: 50%;
  height: 3px;
  background-color: rgb(255, 255, 255);
}

.description {
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 15px;
}

.about-content h1 {
  font-size: xx-large;
  color: rgb(6, 222, 225);
  text-align: center;
}

.description p {
  font-size: large;
  line-height: 30px;
  text-align: justify;
}
